<template>
	<div :class="$style.nav9">
		<div :class="$style.titleImg">
			<img src="./title0.png" />
		</div>
		<box :class="[$style.box,$style.box0]">
			<span slot="title"></span>
			<div :class="$style.pendant"></div>
			<p v-for="(v,i) in data.content0" v-html="v" :class="$style.p"></p>
		</box>
		<div :class="$style.titleImg">
			<img src="./title1.png" />
		</div>
		<box :class="[$style.box,$style.box1]">
			<stars :data="data.content1"></stars>
		</box>
		<div :class="$style.titleImg">
			<img src="./title2.png" />
		</div>
		<box :class="[$style.box,$style.box2]">
			<div :class="$style.pendant"></div>
			<p v-for="(v,i) in data.content2" v-html="v" :class="$style.p"></p>
		</box>
		<div :class="$style.titleImg">
			<img src="./title3.png" />
		</div>
		<box :class="[$style.box,$style.box3]">
			<div :class="$style.pendant"></div>
			<p v-for="(v,i) in data.content3" v-html="v" :class="$style.p"></p>
		</box>
		<div :class="$style.titleImg">
			<img src="./title4.png" />
		</div>
		<box :class="[$style.box,$style.box4]" v-for="(v,i) in data.content4" :key="i">
			<p v-html="v" :class="$style.p"></p>
		</box>
		<div :class="$style.titleImg">
			<img src="./title5.png" />
		</div>
		<box :class="[$style.box,$style.box5]">
			<p v-for="(v,i) in data.content5" :class="$style.p">
				<span :class="$style.title">{{v.title}}</span>
				<StarScore :class="$style.StarScore" :key="'StarScore'+i" :value="v.value"></StarScore>
				<span :class="$style.value">{{v.value}}</span>
			</p>
		</box>
		<box :class="[$style.box,$style.box6]" v-for="(v,i) in data.content5" :key="'box6'+i">
			<p :class="$style.title">
				<span>{{v.title}}</span>
			</p>
			<div :class="[$style.num,$style['color'+i]]">
				{{v.value}}
			</div>
			<p v-html="v.content" :class="$style.p"></p>
		</box>
	</div>
</template>

<script>
	var component = {
		name: 'nav9', //
		props: {

		},
		data() {
			return {
				data: {
					content0: [
						'',
						'',
					],
					content1: [{
						title: '',
						content: [''],
						type: 1,
					}, {
						title: '',
						content: ['', '', '', '', ''],
						type: 0,
					}, {
						title: '',
						content: ['', '', ''],
						type: 1,
					}, {
						title: '',
						content: ['', '', '', '', '', '', '', '', ''],
						type: 0,
					}],
					content2: [
						'',
					],
					content3: [
						'30',
						'',
					],
					content4: [
						'',
						'',
					],
					content5: [{
						title: '',
						value: 90,
						content: '',
					}, {
						title: '',
						value: 95,
						content: '',
					}, {
						title: '',
						value: 75,
						content: '',
					}],
				},
			};
		},
		computed: { //
			data_computed() {
				return {

				}
			}
		},
		watch: { //

		},
		components: { //
			titleImg() {
				return import('../titleImg')
			},
			box() {
				return import('../box')
			},
			stars() {
				return import('../stars')
			},
			StarScore() {
				return import('../StarScore')
			}
		},
		methods: { //

		},
		filters: { //

		},
		beforeRouteEnter(to, fo, next) { //
			next();
		},
		beforeCreate() { //vue

		},
		created() { //

		},
		beforeMount() { //

		},
		mounted() { //

		},
		beforeRouteUpdate(to, fo, next) { //
			next();
		},
		beforeUpdate() { //

		},
		updated() { //

		},
		beforeRouteLeave(to, fo, next) { //
			next();
		},
		beforeDestroy() { //

		},
		destroyed() { //

		},
	};
	export default component;
</script>

<style module lang="stylus">
	.nav9 {
		padding-left: .5rem;
		padding-right: @padding-left;
		overflow-x: hidden;
		.title-img {
			margin-top: .5rem;
			img {
				width: 100%;
				display: block;
			}
		}
		.box {
			margin-top: 1rem;
			.p {
				position: relative;
				strong {
					color: red;
					text-decoration: underline;
				}
			}
			&.box0 {
				margin-top: 2rem;
				.pendant {
					width:3.525rem;
					height:3.425rem;
					background-image: url(./pendant0.png);
					background-size: 100% 100%;
					position: absolute;
					top: 0;
					right: 0;
					z-index: 0;
					transform: translate(10%, -50%);
				}
			}
			&.box1 {
			}
			&.box2{
				.pendant {
					width:3.525rem;
					height:3.425rem;
					max-width: 100%;
					background-image: url(./pendant0.png);
					background-size: 100% 100%;
					position: absolute;
					bottom: 0;
					right: 0;
					z-index: 0;
					transform: translate(0%, 10%);
				}
			}
			&.box3{
				padding-bottom: 2rem;
				.pendant {
					width: 17.625rem;
					height:2.45rem;
					max-width: 100%;
					background-image: url(./pendant1.png);
					background-size: 100% 100%;
					position: absolute;
					bottom: 0;
					right: 0;
					z-index: 0;
					transform: translate(0%, 0%);
				}
			}
			&.box2,
			&.box3,
			&.box4 {
				.p {
					strong {
						color: red;
						text-decoration: underline;
					}
					+.p {
						margin-top: .5rem;
					}
				}
			}
			&.box5 {
				.p {
					display: flex;
					align-items: center;
					.title {
						flex: 0 0 auto;
						width: 5.85rem;
						text-align: right;
						margin-right: .5rem;
						font-size: 0.65rem;
					}
					.StarScore {
						flex: 1 1 auto;
					}
					.value {
						margin-left: .5rem;
						flex: 0 0 auto;
						font-size: 0.9rem;
					}
				}
			}
			&.box6 {
				.title {
					text-align: center;
					span {
						display: inline-block;
						position: relative;
						padding: 0 2rem;
						&:after {
							content: '';
							display: inline-block;
							width: 1.75rem;
							height: 0.9rem;
							background-image: url(../TransverseProgressBarTitle/titleIcon.png);
							background-size: 100% 100%;
							position: absolute;
							top: 50%;
							right: 0;
							transform: translate(0, -50%);
						}
						&:before {
							content: '';
							display: inline-block;
							width: 1.75rem;
							height: 0.9rem;
							background-image: url(../TransverseProgressBarTitle/titleIcon.png);
							background-size: 100% 100%;
							position: absolute;
							top: 50%;
							left: 0;
							transform: translate(0, -50%)rotateY(180deg);
						}
					}
				}
				.num {
					width: 100%;
					height: 3.025rem;
					background-size: auto 100%;
					background-repeat: no-repeat;
					background-position: center center;
					text-align: center;
					color: white;
					font-size: 1.2rem;
					line-height: 3.025rem;
					margin: auto;
					margin-top: .5rem;
					margin-bottom: .5rem;
					&.color0 {
						background-image: url(./num0_bg.png);
					}
					&.color1 {
						background-image: url(./num1_bg.png);
					}
					&.color2 {
						background-image: url(./num2_bg.png);
					}
					&.color3 {
						background-image: url(./num3_bg.png);
					}
					&.color4 {
						background-image: url(./num4_bg.png);
					}
				}
				.TransverseProgressBarC {
					margin-top: .5rem;
					margin-bottom: 1rem;
				}
				.p {
					strong {
						color: red;
						text-decoration: underline;
					}
				}
			}
		}
	}
</style>


